ul{
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none;
    display: block;
}

.FavoriteCity {
    margin: 15px;
    width: 85%;
    height: 80px;
    background-color: rgba(94, 192, 132, 0.5);
    border: 0px solid rgba(0, 0, 0, 0);
    border-radius: 5px 5px 5px 5px;
    color: #FFFFFF;
}

.FavoriteCityImage {
    display: block;
}

.FavoriteCityTemperature {
    display: inline;
    position: relative;
    top: -40px;
    left: 50px;
    font: 28pt sans-serif;
}

.FavoriteCityName {
    display: block;
    position: relative;
    top: -40px;
    left: 20px;
}

.FavoriteCityButton{
    position: relative;
    top: -85px;
    left: -10px;
    float: right;
    width: 50px;
    height: 50px;
    background-image: url("./img/delete.png");
    background-repeat: no-repeat;
    background-size: 50px;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    cursor: pointer;
}

.Favorite {
    float: left;
    width: 25%;
    height: 100vh;
    background-color: #FFFFFF;
    background-image: url(./img/favorites.jpg);
    background-size:100% 100%;
}

.Filter {
    height: 45px;
    background-color: #5B7A5A;
    overflow: hidden;
}

.Filter input{
    height: 30px;
    width: 80%;
    color: #FFFFFF;
    background-color: #5B7A5A;
    border: 0.5px solid rgb(123, 163, 121);
    border-radius: 5px 5px 5px 5px;
    float: left;
    margin-top: 5px;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
}

.Filter input::-webkit-input-placeholder {
    color: #EEEEEE;
}

.Filter input:-ms-input-placeholder {
    color: #EEEEEE;
}

.Filter input::-ms-input-placeholder {
    color: #EEEEEE;
}

.Filter input::placeholder {
    color: #EEEEEE;
}

.Filter button{
    height: 35px;
    width: 35px;
    border: 0;
    background-color: #5B7A5A;
    background-image: url(./img/search.jpg);
    float: right;
    margin-top: 2px;
    margin-right: 5px;
    cursor: pointer;
}

.FavoriteList {
    height: 90vh;
    margin-top: 15px;
    margin-left: 15px;
    margin-right: 15px;
    background-color: rgba(256, 256, 256, 0.3);
    border: 0;
    border-radius: 5px 5px 5px 5px;
    overflow: scroll;
}

.FavoriteList::-webkit-scrollbar {
    width: 12px;
    height: 0px;
}
 
.FavoriteList::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
.FavoriteList::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}


