ul{
    margin: 0;
    padding: 0;
}

li {
    list-style-type: none;
    display: inline;
}

.City {
    display: inline-block;
    margin-left: 15px;
    margin-top: 15px;
    width: 350px;
    height: 100px;
    border: 0px solid rgba(46, 46, 46, 0.1);
    border-radius: 25px 5px 25px 25px;
    background-color: rgba(0, 110, 185, 0.5);
    background: -webkit-gradient(linear, left bottom, left top, from(#daebf8), to(#5ca6f5));
    background: -webkit-linear-gradient(bottom, #daebf8, #5ca6f5);
    background: -o-linear-gradient(bottom, #daebf8, #5ca6f5);
    background: linear-gradient(to top, #daebf8, #5ca6f5);
    color: #FFFFFF;
}

.CityImage {
    display: inline;
}

.CityName {
    display: block;
    position: relative;
    top: -40px;
    left: 50px;
    font: 18pt sans-serif;
}

.CityCountry{
    display: block;
    float: right;
    position: relative;
    top: -50px;
    right: 15px;
    font: 12pt sans-serif;
}

.CityTemperature{
    display: block;
    float: right;
    position: relative;
    top: -40px;
    right: 60px;
    font: 28pt sans-serif;
}

.CityWind{
    display: block;
    position: relative;
    top: -20px;
    left: 20px;
    font: 10pt sans-serif;
}

.CityPressure{
    display: inline;
    position: relative;
    top: -20px;
    left: 20px;
    font: 10pt sans-serif;
}

.CityHumidity{
    display: inline;
    position: relative;
    top: -20px;
    left: 140px;
    font: 10pt sans-serif;
}

.CityRemove{
    position: relative;
    top: -80px;
    left: 20px;
    float: right;
    width: 25px;
    height: 25px;
    background-image: url("./img/remove.png");
    background-repeat: no-repeat;
    background-size: 25px;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    cursor: pointer;
}

.CityAddFavorite{
    position: relative;
    top: -80px;
    left: 15px;
    float: right;
    width: 25px;
    height: 25px;
    background-image: url("./img/favorite.png");
    background-repeat: no-repeat;
    background-size: 25px;
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    cursor: pointer;
}

.Content{
    height: 100vh;
    background-color: burlywood;
    background-image: url(./img/content.jpg);
    background-size:100% 100%;
    float: right;
    width: 75%;
}

.Header {
    height: 45px;
    background-color: #668863;
}

.Title {
    color: #FFFFFF;
    padding-top: 15px;
    padding-left: 10px;
    font: 24pt/10pt sans-serif;
}

.Search {
    margin-top: 30px;
    color: #FFFFFF;
    background-color: rgba(186, 223, 253, 0.459);
    border: 0px solid #FFFFFF;
    border-radius: 5px 5px 5px 5px;
    width: 60%;
    height: 50px;
    margin-left: 10px;
    overflow: hidden;
}

.Search input{
    height: 30px;
    width: 80%;
    color: rgb(76, 93, 109);
    background-color: rgba(255, 255, 255, 0.4);
    border: 0.5px solid #7094e4;
    border-radius: 5px 5px 5px 5px;
    float: left;
    margin-top: 8px;
    margin-left: 5px;
    padding-left: 5px;
    padding-right: 5px;
}

.Search input::-webkit-input-placeholder {
    color: rgb(161, 184, 204);
}

.Search input:-ms-input-placeholder {
    color: rgb(161, 184, 204);
}

.Search input::-ms-input-placeholder {
    color: rgb(161, 184, 204);
}

.Search input::placeholder {
    color: rgb(161, 184, 204);
}

.Search button{
    position: absolute;
    float: right;
    height: 35px;
    width: 7%;
    margin-top: 7px;
    margin-right: 5px;
    margin-left: 5px;
    border: 0;
    border-radius: 5px 5px 5px 5px;
    background-color: rgb(19, 101, 255);
    cursor: pointer;
    color: #FFFFFF;
    overflow: hidden;
}

.Search button:hover{
    position: absolute;
    float: right;
    height: 35px;
    width: 7%;
    margin-top: 7px;
    margin-right: 5px;
    margin-left: 5px;
    border: 0;
    border-radius: 5px 5px 5px 5px;
    background-color: rgb(14, 77, 194);
    cursor: pointer;
    color: #FFFFFF;
    overflow: hidden;
}

.ContentList {
    height: 80vh;
    margin-top: 90px;
    margin-left: 15px;

    background-color: rgba(256, 256, 256, 0.2);
    border: 0;
    border-radius: 5px 5px 5px 5px;
    overflow: scroll;
}

.ContentList::-webkit-scrollbar {
    width: 12px;
    height: 0px;
}
 
.ContentList::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
.ContentList::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}


